.singer {
    background-color: #f2f3f4;
}

.singer .classify {
    display: flex;
    align-items: center;
    width: 100%;
    height: 35px;
    padding-left: 3px;
    overflow: hidden;
}

.singer .classify ul{
    display: flex;
}
.singer .classify ul li{
    text-align: center;
    width: 60px;
    height: 25px;
    line-height: 25px;
    margin: 0 5px;
    overflow: hidden;
}
.singer .classify ul .title{
    width: 85px;
    font-size: 12px;
    margin: 0;
    color: #7b7b7c;
}
.singer .classify ul li.activate{
    border: 1px solid #d76c64;
    border-radius: 5px;
    color: #d76c64;
}

.singer .letter {
    display: flex;
    align-items: center;
    width: 100%;
    height: 20px;
    padding-left: 3px;
    overflow: hidden;
    margin-bottom: 5px;
}

.singer .letter ul{
    display: flex;
}
.singer .letter ul li{
    text-align: center;
    width: 20px;
    height: 20px;
    line-height: 20px;
    margin: 0 5px;
    overflow: hidden;
}
.singer .letter ul .title{
    width: 40px;
    font-size: 12px;
    margin: 0;
    color: #7b7b7c;
}
.singer .letter ul li.letter-activate{
    color: #d76c64;
}
.singer .list {
    padding-bottom: 100px;
}
.singer .list .am-list-item {
    height: 62px;
    background-color: #f2f3f4;
}
.singer .list .am-list-item.am-list-item-active {
    background-color: #ebebeb;
}
.singer .list .am-list-item .am-list-thumb img{
    width: 50px;
    height: 50px;
    border-radius: 5px;
    background-image: url("https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=891174751,554927679&fm=26&gp=0.jpg");
    background-size: 100% 100%;
}