
.animation.icon-yinle {
    position: fixed;
    top: 200px;
    left: 200px;
    z-index: 999;
    font-size: 14px;
    transition: all 1.5s;
    color: #d4473e;
    animation: rotate 3s linear infinite;
    opacity: 1;
}


@keyframes rotate {
    0%{
        transform: rotate(0)
    }
    100%{
        transform: rotate(360deg)
    }
}